@import "ScssHelpers/_helpers_all.scss";

.post_content {
	@include bp($point_2, min) {
		padding: 11.6rem var(--gl-indent) 12rem;
	}

	@include bp($point_3) {
		padding: 4rem var(--gl-indent);
	}
}

.post_content__block {
	@extend %global_flex_block_row_wrap_flex-start_stretch;

	position: relative;
}

// sidebar

.post_content__sidebar {
	position: relative;
	width: 25.8%;

	@include bp($point_3) {
		display: none;
	}
}

.post_content__sidebar_list {
	position: relative;

	padding: 1rem 2.4rem 0;
	border-left: 1px solid var(--border_grey_color);

	z-index: 25;

	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		height: 9.4rem;
		width: 1px;
		background-color: var(--blue_color_2);
	}

	&.post_content__sidebar_list--active {
		position: fixed;
		top: 0;
		width: calc((100% - var(--gl-indent)*2) * 0.25);
	}

	&.post_content__sidebar_list--finish {
		position: absolute;
		bottom: 0;
		width: 100%;
	}
}

.post_content__sidebar_item {
	margin-bottom: 1.4rem;

	&:last-child {
		margin: 0;
	}
}

.post_content__sidebar_title {
	font-size: 2rem;
	color: var(--gray_color);
	font-weight: 400;
	transition: color .3s;
	cursor: pointer;

	&.post_content__sidebar_title--important_mod {
		margin-top: 2rem;
		color: var(--primary_color);

		.post_content__sidebar_item:first-child & {
			margin: 0;
		}
	}
}

.post_content__sidebar_link {
	color: currentColor;

	&.post_content__sidebar_link--active,
	&:hover {
		color: var(--blue_color_2);
		text-decoration: none;
	}
}

// article

.post_content__article {
	letter-spacing: -.02em;
	
	@include bp($point_2, min) {
		width: 48.8%;
		padding: 0 9.6rem;
	}

	@include bp($point_3) {
		width: 100%;
		margin-bottom: 4.4rem;

		&:last-child {
			margin: 0;
		}
	}

	h2 {
		font-weight: 600;
		color: var(--primary_color);
		
		@include bp($point_2, min) {
			margin-bottom: 5rem;
			
			font-size: 3.2rem;
		}

		@include bp($point_3) {
			margin-bottom: 2.6rem;

			font-size: 2.2rem;
			line-height: 1.2;
		}
	}

	h3 {
		
		font-weight: 600;
		color: var(--primary_color);
		
		@include bp($point_2, min) {
			max-width: 90%;
			margin-bottom: 3.8rem;

			font-size: 2.6rem;
		}

		@include bp($point_3) {
			margin-bottom: 2.2rem;

			font-size: 1.7rem;
		}
	}

	p {
		color: var(--blue_color);
		
		@include bp($point_2, min) {
			margin-bottom: 3rem;
			font-size: 2rem;
			line-height: 1.5;
		}

		@include bp($point_3) {
			margin-bottom: 2rem;

			font-size: 1.6rem;
			line-height: 1.35;
		}

		&:last-child {
			margin: 0;
		}
	}

	code {
		font-weight: 500;
		font-family: $font_3;
		
		@include bp($point_2, min) {
			font-size: 1.8rem;
		}

		@include bp($point_3) {
			font-size: 1.4rem;
		}
	}

	a {
		color: var(--blue_color_2);
		transition: color .3s;

		&:hover, &:focus {
			color: var(--orange_color);
		}
	}

	figure {
		display: block;
		width: 100%;
		
		@include bp($point_2, min) {
			margin: 8.6rem 0;
		}

		@include bp($point_3) {
			margin: 4rem 0 4rem;
		}
	}

	img {
		display: block;
		
		@include bp($point_2, min) {
			margin: 0 -9.6rem;
			width: calc(100% + 19.2rem);
		}

		@include bp($point_3) {
			width: 100%;
		}
	}

	figcaption {
		display: block;
		width: 100%;

		color: var(--gray_color);
		text-align: center;

		@include bp($point_2, min) {
			margin-top: 2.6rem;

			font-size: 1.8rem;
		}

		@include bp($point_3) {
			margin-top: 1.8rem;
			font-size: 1.4rem;
		}
	}

	table {
		text-align: left;
		border-top: 2px solid var(--primary_color);
		
		@include bp($point_2, min) {
			margin: 8.6rem -9.6rem 8.4rem;
		}

		@include bp($point_3) {
			margin: 4rem 0 4rem;
		}
	}

	tr {
		padding: 2rem 0;
		border-bottom: 1px solid var(--light_blue_3);
	}

	th {
		padding: 2.7rem 1rem 3rem 0;
		font-size: 2.2rem;
		font-weight: 400;
		color: var(--blue_color);

		@include bp($point_3) {
			font-size: 1.4rem;
		}
	}

	td {
		padding: 2.4rem 1rem 2.8rem 0;

		font-size: 2rem;
		line-height: 1.4;
		color: var(--blue_color);

		@include bp($point_3) {
			font-size: 1.2rem;
		}

		&:last-child {
			color: var(--primary_light_color);
		}
	}

	th:nth-child(1), 
	td:nth-child(1) {
		width: 31.2%;
	}

	th:nth-child(2),
	td:nth-child(2) {
		width: 47%;
	}

	th:nth-child(3),
	td:nth-child(3) {
		width: 21.8%;
		text-align: right;
		padding-right: 0;
	}

	blockquote {
		display: block;

		line-height: 1.35;
		color: var(--primary_color);

		border-top: 1px solid var(--blue_color_3);
		border-bottom: 1px solid var(--blue_color_3);

		@include bp($point_2, min) {
			margin: 7.8rem 0 7rem;
			padding: 4rem 12rem 5rem 0;
			font-size: 3.2rem;
		}

		@include bp($point_3) {
			margin: 4rem 0;
			padding: 3rem 0;
			font-size: 2rem;
		}
	}
}

.post_content__form {
	position: relative;
	background-color: var(--green_color);
	
	@include bp($point_2, min) {
		margin: 8.6rem -9.6rem 8rem;
		padding: 10rem 14rem 7.8rem 15.6rem;
		border-radius: 1rem;
	}

	@include bp($point_3) {
		padding: 6.4rem var(--gl-indent);
		margin: 4rem calc(var(--gl-indent) * -1);
	}
}

// social

.post_content__social_col {
	@include bp($point_2, min) {
		margin-left: auto;
	}
}

.post_content__social {
	@include bp($point_2, min) {
		margin-top: 1rem;
	}

	@include bp($point_3) {
		@include flex_block(row, wrap, center);
	}

	&.post_content__social--active {
		position: fixed;
		top: 0;
		right: calc(var(--gl-indent) - 1rem);

		@media (hover: none) and (pointer: coarse) {
			right: var(--gl-indent);
		}
	}

	&.post_content__social--finish {
		position: absolute;
		bottom: 0;
		right: 0;
	}
}

.post_content__social_item {
	@include bp($point_2, min) {
		margin-bottom: 1.4rem;
	}

	@include bp($point_3) {
		margin-right: 1.4rem;
	}

	&:last-child {
		margin: 0;
	}
}

.post_content__social_link {
	@extend %global_flex_block_row_nowrap_center_center;
	width: 4.4rem;
	height: 4.4rem;

	color: var(--gray_color);
	border: 1px solid var(--border_grey_color);

	transition: all .3s;

	&.post_content__social_link--link {
		&:hover, &:focus {
			color: var(--orange_color);
			border-color: var(--orange_color);
		}
	}

	&.post_content__social_link--twitter {
		&:hover, &:focus {
			color: #1DA1F2 ;
			border-color: #1DA1F2;
		}
	}

	&.post_content__social_link--facebook {
		&:hover, &:focus {
			color: #4267B2;
			border-color: #4267B2;
		}
	}

	&.post_content__social_link--whatsapp {
		&:hover, &:focus {
			color: #25D366 ;
			border-color: #25D366;
		}
	}
}

.post_content__social_icon {
	width: 2.2rem;
	height: 2.2rem;
}