@import "ScssHelpers/helpers_all";

.post_hero {
	background-color: var(--light_color);

	@include bp($point_2, min) {
		padding: 15rem var(--gl-indent) 13rem;
	}

	@include bp($point_3) {
		padding: 2.4rem var(--gl-indent);
	}
}

.post_hero__in {
	max-width: 144rem;
	margin: 0 auto;
}

.post_hero__cols {
	@extend %global_flex_block_row_wrap_flex-start_stretch;
}

.post_hero__col {
	&:first-child {
		@include bp($point_2, min) {
			width: 45%;
		}

		@include bp($point_3) {
			margin-bottom: 3rem;
		}
	}

	&:last-child {
		@extend %global_flex_block_column_nowrap_flex-start_stretch;

		@include bp($point_2, min) {
			width: 55%;
			padding: 0 10rem;
		}
	}

	@include bp($point_3) {
		width: 100%;
	}
}

.post_hero__back {
	@extend %global_flex_block_row_nowrap_flex-start_center;
	
	color: var(--blue_color);
	
	@include bp($point_2, min) {
		margin-bottom: 9.2rem;
		font-size: 2rem;
	}

	@include bp($point_3) {
		margin-bottom: 3.4rem;
		font-size: 1.9rem;
	}

	&:hover, &:focus {
		text-decoration: none;
	}
}

.post_hero__back_icon_in {
	
	@include bp($point_2, min) {
		height: 1.6rem;
		width: 1rem;
	}

	@include bp($point_3) {
		width: .6rem;
		height: 1rem;
	}
}

.post_hero__back_icon {
	@extend %global_flex_block_row_nowrap_center_center;

	margin-right: 1rem;

	color: var(--blue_color);

	border-radius: 50%;
	background-color: rgba(238, 243, 249, 0.75);

	transition: all .3s;

	.post_hero__back:hover &,
	.post_hero__back:focus & {
		background-color: var(--blue_color);
		color: var(--white_color);
	}

	@include bp($point_2, min) {
		width: 4.6rem;
		height: 4.6rem;
	}

	@include bp($point_3) {
		width: 3.2rem;
		height: 3.2rem;
	}
}

.post_hero__info {
	@extend %global_flex_block_row_wrap_flex-start;

	@include bp($point_2, min) {	
		margin-bottom: 3rem;
	}

	@include bp($point_3) {
		margin-bottom: 2rem;
	}
}

.post_hero__info_item {
	position: relative;
	@include bp($point_2, min) {
		padding-right: 2rem;
		margin-right: 1.4rem;
	}

	@include bp($point_3) {
		padding-right: 1.6rem;
		margin-right: 1rem;
	}

	&:after {
		content: '';
		position: absolute;
		top: 50%;
		right: 0;

		width: .4rem;
		height: .4rem;
		margin-top: -.2rem;

		background-color: var(--gray_color);
	}

	&:last-child {
		padding: 0;
		margin: 0;

		&:after {
			display: none;
		}
	}
}

.post_hero__info_link {
	margin-right: .6rem;
	letter-spacing: -.05rem;
	color: var(--gray_color);
	
	@include bp($point_2, min) {
		font-size: 1.8rem;
	}

	@include bp($point_3) {
		font-size: 1.3rem;
	}

	&:after {
		content: ',';
	}

	&:last-child {
		margin-right: 0;
	
		&:after {
			display: none;
		} 
	}
}

.post_hero__info_date {
	color: var(--gray_color);

	@include bp($point_2, min) {
		font-size: 1.8rem;
	}

	@include bp($point_3) {
		font-size: 1.3rem;
	}
}

.post_hero__title {
	@extend %h6;

	letter-spacing: -.014em;
	
	@include bp($point_2, min) {
		margin-bottom: 8.6rem;
	}

	@include bp($point_3) {
		margin-bottom: 3rem;
		font-size: 2.5rem;
	}
}

.post_hero__author {
	@extend %global_flex_block_row_nowrap_flex-start_center;

	@include bp($point_2, min) {
		margin-bottom: 1.8rem;
	}

	@include bp($point_3) {
		margin: 0;
	}
}

.post_hero__author_pic {
	display: block;
	
	@include bp($point_2, min) {
		width: 5.4rem;
		height: 5.4rem;
		margin-right: 2.2rem;
	}

	@include bp($point_3) {
		width: 4rem;
		height: 4rem;
		margin-right: 1.8rem;
	}
}

.post_hero__author_img {
	@extend %cover_img;
}

.pos_hero__author_name {
	color: var(--blue_color);
	
	@include bp($point_2, min) {
		font-size: 2.4rem;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
	}
}

.post_hero__bottom {
	max-width: 37rem;

	padding-top: 1rem;
	border-top: 1px solid var(--gray_4_color);

	letter-spacing: -.01rem;
	color: var(--gray_color);
	
	@include bp($point_2, min) {
		font-size: 1.8rem;
	}

	@include bp($point_3) {
		display: none;
		font-size: 1.3rem;
	}
}

.post_hero__image {
	position: relative;

	display: block;
	max-width: 100%;
	
	@include bp($point_2, min) {
		width: 44.6rem;
		margin: auto 0 2.4rem auto;
	}

	@include bp($point_3, $point_4) {
		max-width: 44rem;
	}

	@include bp($point_3) {
		width: 100%;
		margin: 0 auto;
	}

	&:before {
		content: '';
		display: block;
		padding-bottom: 100%;
	}
}

.post_hero__image_pic {
	position: absolute;
	top: 0;
	left: 0;

	display: block;
	width: 100%;
	height: 100%;
}

.post_hero__image_img {
	@extend %cover_img;
}

.post_hero__image_decor {
	position: absolute;

	background-color: var(--dot-color);

	opacity: .4;
	z-index: -1;

	@include bp($point_3) {
		display: none;
	}

	&.post_hero__image_decor--v1 {
		bottom: -5.2rem;
		left: -5.2rem;

		width: 10.4rem;
		height: 10.4rem;
	}

	&.post_hero__image_decor--v2 {
		top: -5.2rem;
		right: -5.2rem;

		width: 10.4rem;
		height: 10.4rem;
	}

	&.post_hero__image_decor--v3 {
		top: 5.2rem;
		left: calc(100% + 5.2rem);

		width: 5.2rem;
		height: 5.2rem;
	}
}