@import 'ScssHelpers/_helpers_all.scss';

.subscription {
	@extend %global_flex_block_row_wrap_center_center;


	@include bp($point_2, min) {
		min-height: 95.4rem;
		padding: 12rem var(--gl-indent) 11.5rem;
	}
	@include bp($point_3) {
		min-height: 45.4rem;
		padding: 9rem var(--gl-indent);
	}

	&::after,
	&::before {
		z-index: 1;
		position: absolute;
		top: 0;
		height: 100%;

		@include bp($point_2, min) {
			width: 50%;
		}
	}

	&::after {
		left: 0;
		background-color: var(--primary_color);

		@include bp($point_2, min) {
			content: '';
		}
	}

	&::before {
		background-color: var(--green_color);
		content: '';

		@include bp($point_2, min) {
			left: 50%;
		}
		@include bp($point_3) {
			width: 100%;
			left: 0;
		}
	}
}

.subscription_in {
	@extend %global_flex_block_row_wrap_flex-start_stretch;

	z-index: 2;
	position: relative;
	max-width: $page_width_2;
}

.subscription_col {
	position: relative;

	@include bp($point_2, min) {
		width: 50%;
	}
	@include bp($point_3) {
		margin: 0 auto;
		max-width: 70rem;
	}

	&:nth-child(1) {
		padding-right: 17.6rem;

		@include bp($point_3) {
			display: none;
		}
	}
	&:nth-child(2) {
		@extend %global_flex_block_row_wrap_flex-start;

		@include bp($point_2, min) {
			align-items: normal;
			align-content: normal;
			padding-left: 17.6rem;
		}
	}
}

.subscription_heading {
	max-width: 47.6rem;
	width: 100%;
}

.subscription_heading__icon {
	margin-bottom: 6rem;
	width: 10.2rem;
	height: 11rem;
}

.subscription_heading__title {
	@extend %h6;

	color: var(--white_color);

	span {
		color: var(--primary_light_color)
	}
}

.subscription_form {

}

.subscription_form__title {
	@extend %h3;

	width: 100%;

	color: var(--white_color);

	@include bp($point_2, min) {
		max-width: 32rem;
		margin-bottom: 6.6rem;

		.post_content__form & {
			max-width: 100%;
			margin-bottom: 6rem;
			text-align: center;
		}
	}
	@include bp($point_3, $point_4) {
		text-align: center;
		font-size: 2.4rem;
		margin-bottom: 5.6rem;
	}
	@include bp($point_4 - 1) {
		margin-bottom: 3.6rem;
	}
}

.subscription_form__in {

}

.subscription_form__agreement {
	color: var(--border_grey_color);

	@include bp($point_4, min) {
		font-size: 1.6rem;
	}
	@include bp($point_4 - 1) {
		font-size: 1.4rem;
	}

	.post_content__form & p {
		font-size: 1.6rem;
		color: var(--border_grey_color);
	}

	a {
		color: var(--white_color);
		cursor: pointer;

		transition: color .2s ease-in-out;

		&:hover,
		&:focus {
			text-decoration: none;
			color: var(--primary_color);
		}
	}
}


.subscription_form__checkboxes {
	max-width: 47.2rem;
	width: 100%;

	&:not(:last-child) {


		@include bp($point_2, min) {
			margin-bottom: 10.2rem;
		}
		@include bp($point_3, $point_4) {
			margin-bottom: 7.2rem;
		}
		@include bp($point_4 - 1) {
			margin-bottom: 4.2rem;
		}
	}

	.post_content__form & {
		margin-bottom: 7.8rem;
		max-width: 49.8rem;
		
		@include bp($point_2, min) {
			padding-left: 3.4rem;
		}
	}
}

.subscription_form__checkbox {
	&:not(:last-child) {
		margin-bottom: 2.8rem;
	}
}

.subscription_form__field {
	width: 100%;

	@include bp($point_2, min) {
		max-width: 40.6rem;
	}

	&:not(:last-child) {
		margin-bottom: 2.6rem;
	}

	.post_content__form & {
		margin: 0 auto 2.6rem;
	}
}

.subscription_form__input {
	position: relative;
	width: 100%;
}

.subscription_form__input_btn {
	@extend %global_flex_block_row_wrap_center_center;

	position: absolute;

	color: var(--white_color);

	border-radius: 50%;
	cursor: pointer;
	background-color: var(--primary_color);

	transition: background-color .3s ease;


	.subscription_form__field--typing_state &,
	.subscription_form__field--error_state & {
		background-color: var(--gray_2_color);
	}

	&:hover {
		background-color: var(--green_color);
	}

	@include bp($point_4, min) {
		top: .8rem;
		right: .8rem;

		width: 4.8rem;
		height: 4.8rem;
		padding: 1.4rem;
	}

	@include bp($point_4 - 1) {
		top: 1.2rem;
		right: 1.2rem;

		width: 3rem;
		height: 3rem;
		padding: .9rem;
	}
}
//---------------------------------------------Success state

.subscription_success {
	@extend %global_flex_block_row_nowrap_center_center;
	@extend %transition_opacity;

	position: absolute;
	width: 100%;
	height: 100%;

	text-align: center;
	margin: 0 auto;

	background: var(--green_color);
	opacity: 0;

	pointer-events: none;

	.post_content__form & {
		top: 0;
		left: 0;
	}

	&.subscription_success--open_state {
		opacity: 1;

		pointer-events: auto;
	}
}

.subscription_success__in {
	@include bp($point_4, min) {
		padding: 0 11rem 8rem 0;

		.post_content__form & {
			padding: 0;
		}
	}
}

.subscription_success__icon {
	@include bp($point_4, min) {
		margin: 0 0 1.4rem auto;
		width: 10.6rem;
		height: 7.4rem;
	}

	@include bp($point_4 - 1) {
		margin: 0 auto 1.4rem;
		width: 8rem;
		height: 6rem;
	}
}

.subscription_success__title {
	@extend %h6;

	color: var(--white_color);

	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
	@include bp($point_4 - 1) {
		font-size: 3.6rem;

		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}

}

.subscription_success__descr {
	font-size: 2rem;
	color: var(--white_color);
}


//---------------------------------------------Success state###
