%cover_img {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: cover;
}

%contain_img {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: contain;
}

%slider_head {
	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, center, center);

		&:not(:last-child) {
			margin-bottom: 8.6rem;
		}
	}

	@include bp($point_3) {
		display: none;
	}
}

%slider_title {
	@extend %h6;

	letter-spacing: -.03em;
	font-family: $biotif_font;
}


%slider_nav {
	margin-left: auto;
	margin-bottom: 0.1rem;

	@include flex_block(row, nowrap, flex-start, center, center);
}

%slider_nav_arrow {
	width: 4.7rem;
	height: 4.7rem;
	border-radius: 50%;

	background-color: var(--blue_color);
	color: var(--white_color);
	cursor: pointer;

	&:not(:last-child) {
		margin-right: 2rem;
	}

	@extend %transition_color_background;

	&:hover {
		@media (hover: hover) {
			background-color: var(--white_color);
			color: var(--primary_dark_color);
		}
	}

	&:active {
		@media (hover: none) {
			background-color: var(--white_color);
			color: var(--primary_dark_color);
		}
	}

	@include flex_block(row, nowrap, center, center, center);
}

%slider_nav_icon {
	display: block;
	width: 1rem;
	height: 1.5rem;
}

%slider_nav_progress {
	@extend %global_flex_block_row_nowrap_flex-start;

	margin: 0 1.2rem;

	flex-grow: 1;
	height: 0.2rem;

	background-color: var(--border_grey_color);

	.swiper-pagination-progressbar-fill {
		flex-grow: 1;
		height: inherit;

		transform-origin: left top;
		background-color: var(--blue_color);
	}
}

%slide_info_item {
	color: var(--gray_color);
	font-family: $biotif_font;
	letter-spacing: -0.02rem;
	line-height: 1.3;

	@include bp($point_2, min) {
		font-size: 1.8rem;
	}

	@include bp($point_3, $point_4) {
		font-size: 1.4rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.3rem;
	}
}

%slide_info_link {
	@extend %transition_color;
	&:hover,
	&:focus {
		text-decoration: none;
		color: var(--primary_light_color);
	}

	&::after {
		content: "";
		display: block;
		width: 0.4rem;
		height: 0.4rem;

		background-color: var(--gray_color);
	}

	@include flex_block(row, nowrap, flex-start, center, center);

	@include bp($point_2, min) {
		&::after {
			margin: 0 1.6rem 0 1.3rem;
		}
	}

	@include bp($point_3, $point_4) {
		&::after {
			margin: 0 1.4rem 0 1.3rem;
		}
	}

	@include bp($point_4 - 1) {
		&::after {
			margin: 0 1rem 0 1.1rem;
		}
	}
}