//--------------------------------------------- HEADINGS
%h1 {
	font-weight: 700;
	letter-spacing: -0.02em;
	// text-transform: uppercase;
	//color: var(--primary_dark_color);

	@include bp($point_2, min) {
		line-height: .93;
		font-size: 14rem;
	}
	@include bp($point_3, $point_4) {
		line-height: 0.91;
		font-size: 6.4rem;
	}

	@include bp($point_4 - 1) {
		line-height: 0.91;
		font-size: 4.7rem;
	}
}

%h2 {
	font-weight: 600;
	color: var(--primary_color);

	@include bp($point_2, min) {
		line-height: 1.25;
		font-size: 3.2rem;
	}
	@include bp($point_3) {
		line-height: 1.31;
		font-size: 1.9rem;
	}
}

%h3 {
	font-weight: 700;
	color: var(--primary_color);

	@include bp($point_2, min) {
		line-height: 1.23;
		font-size: 3rem;
	}
	@include bp($point_3) {
		line-height: 1.31;
		font-size: 1.9rem;
	}
}

%h4 {
	line-height: 1.28;
	letter-spacing: -0.01em;
	color: var(--primary_dark_color);

	@include bp($point_2, min) {
		font-size: 2.8rem;
	}
	@include bp($point_3) {
		font-size: 1.8rem;
	}
}

%h5 {
	font-weight: 600;
	color: var(--primary_color);
	line-height: 1.3;
	font-size: 2.6rem;
}

%h6 {
	line-height: 1.11;
	font-size: 5.4rem;
	font-weight: 600;
	color: var(--primary_dark_color);
}

%accent {
	font-weight: 600;
	color: var(--primary_light_color);

	@include bp($point_2, min) {
		font-size: 5rem;
		line-height: 1.2;
	}
	@include bp($point_3) {
		line-height: 1.1;
		font-size: 3.6rem;
	}
}
//--------------------------------------------- HEADINGS###

//--------------------------------------------- TEXT CONTENTS
%base_text_1 {
	color: var(--blue_color);

	@include bp($point_2, min) {
		font-size: 2rem;
		line-height: 1.5;
	}

	@include bp($point_3) {
		line-height: 1.35;
		font-size: 1.4rem;
	}
}

%base_text_2 {
	font-weight: 500;
	color: var(--gray_color);

	@include bp($point_2, min) {
		font-size: 2rem;
		line-height: 1.5;
	}
	@include bp($point_3) {
		font-size: 1.4rem;
		line-height: 1.35;
	}
}

%caption_1 {
	line-height: 1.35;
	font-weight: 700;
	color: var(--primary_dark_color);

	@include bp($point_2, min) {
		font-size: 2rem;
	}
	@include bp($point_3) {
		font-size: 1.4rem;
	}
}

%caption_2 {
	line-height: 1.35;
	font-weight: 700;
	color: var(--primary_color);

	@include bp($point_2, min) {
		font-size: 2rem;
	}
	@include bp($point_3) {
		font-size: 1.3rem;
	}
}

%caption_3 {
	font-weight: 500;
	color: var(--gray_color);

	@include bp($point_2, min) {
		font-size: 1.8rem;
		line-height: 1.33;
	}
	@include bp($point_3) {
		font-size: 1.3rem;
		line-height: 1.38;
	}
}
//--------------------------------------------- TEXT CONTENTS###

//--------------------------------------------- OTHER
%btn_title {
	font-size: 2.3rem;
	line-height: 1.3;
	letter-spacing: -0.01em;
}

%tag_1 {
	line-height: 1;
	letter-spacing: -0.02em;

	@include bp($point_2, min) {
		font-size: 2.8rem;
		color: var(--gray_color);
	}
	@include bp($point_3) {
		font-size: 1.6rem;
		color: var(--white_color);
	}
}

%tag_2 {
	font-size: 1.8rem;
	color: var(--gray_color);
	line-height: 1.33;
}

%date {
	font-size: 1.8rem;
	line-height: 1;
	color: var(--primary_color);
	font-weight: 600;
}

%author {
	font-size: 2.8rem;
	line-height: 1.28;
	color: var(--primary_dark_color);
}

%dot {
	font-family: Arial, sans-serif;
	color: var(--dot-color);
	line-height: 0.92;

	@include bp($point_2, min) {
		font-size: 14rem;
	}
	@include bp($point_3) {
		font-size: 4.7rem;
	}
}

%code {
	font-size: 1.8rem;
	line-height: 1.55;
	font-weight: 500;
	font-family: "JetBrains Mono", sans-serif;
	color: var(--primary_color);
	background-color: var(--code_bg_color);
}

//--------------------------------------------- OTHER###
